@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #fff;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.flexSB {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.container {
  max-width: 90%;
  margin: auto;
}

button {
  background-color: #fc3;
  outline: none;
  border: none;
  color:#fff;
  padding: 12px 40px;
  font-weight: 500;
  font-size: 17px;
}

.control-btn button {
  background: none;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 0;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
}

.control-btn .next {
  position: absolute;
  top: 50%;
  right: 2%;
}

.control-btn .prev {
  position: absolute;
  top: 50%;
  left: 2%;
  z-index: 99;
}

.heading h1 {
  font-weight: bold;
  margin: 30px 0;
}

.heading a {
  color: #fc3;
}

.MovieBox {
  width: 430px;
  height: 300px;
  position: relative;
  transition: 0.5s;
  color: white;
  border-left: 5px solid transparent;
}

.MovieBox:hover {
  border-left: 5px solid #fc3;
  transform: scale3d(1.1, 1.1, 1) translate3d(6%, 0, 0) perspective(31.25em);
  box-shadow: 0 0 0.73em rgba(0, 0, 0, 0.9);
  cursor: pointer;
}

.MovieBox::after {
    content: "";
    position: absolute;
    width: 430px;
    height: 300px;
    top: 0;
    left: 0;
    z-index: -1;
}

.MovieBox img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -2;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.MovieBox .text {
  padding: 70px 50px;
}

.MovieBox h3 {
  font-weight: 500;
  margin-bottom: 15px;
}

.MovieBox button {
  padding: 12px 20px;
  margin-top: 15px;
  font-size: 13px;
  cursor: pointer;
}

